import { ValidationResultGroup } from 'react-form'

import { useRoot } from '../../../atoms/RootProvider'
import { MAcceptOrder } from '../../../graph/types'
import { useGuardedMutation } from '../../../hooks/useGuardedMutation'
import useRouterContext from '../../../hooks/useRouterContext'
import { useSnackBar } from '../../../hooks/useSnackBar'
import { i18n } from '../../../i18n'
import { AcceptOrderMutation } from './useAcceptOrder.graphql'

export function useAcceptOrder() {
  const { createOrder } = useRoot()
  const { enqueueSuccess, enqueueError } = useSnackBar()
  const { history } = useRouterContext()

  const acceptOrder = useGuardedMutation<
    MAcceptOrder.Mutation,
    MAcceptOrder.Variables
  >(AcceptOrderMutation, {
    getUserErrors: result => result.finishOrder.userErrors,
    refetchQueries: ['GOrderCart'],
  })

  const onSubmit = async () => {
    const result = await acceptOrder(createOrder.acceptOrderVariables)
    if (
      result &&
      result.finishOrder.userErrors.length === 0 &&
      result.finishOrder.order.id === createOrder.orderId
    ) {
      createOrder.resetModel()
      history.replace('/order')
      enqueueSuccess(i18n.t`Order has been accepted`)
    }
  }

  const onSubmitFailure = (errors: ValidationResultGroup<unknown>) => {
    Object.values(errors).forEach(enqueueError)
  }

  return { onSubmit, onSubmitFailure }
}
