import { Trans } from '@lingui/react'
import { autorun } from 'mobx'
import { useDisposable, useObserver } from 'mobx-react-lite'
import React from 'react'

import { useRoot } from '../../../atoms/RootProvider'
import { SelectControl } from '../../../atoms/SelectControl'
import { GOrderPromo } from '../../../graph/types'
import { RM } from '../../../helpers/ramda'
import { createExecutableQuery } from '../../../hooks/useExecutableQuery'
import { i18n } from '../../../i18n'
import { usePromoCodes } from '../hooks/usePromoCodes'
import { OrderFormPromoQuery } from './OrderFormPromo.graphql'

const useExecutableQuery = createExecutableQuery<
  GOrderPromo.Query,
  GOrderPromo.Variables
>(OrderFormPromoQuery)

type TCode = GOrderPromo.PromoCodes

export const OrderFormPromo = () => {
  const { createOrder } = useRoot()
  const { result, execute } = useExecutableQuery()
  const selectedCodes = usePromoCodes()

  useDisposable(() =>
    autorun(() => {
      execute({
        branchId: createOrder.branchId,
      })
    }),
  )

  if (result.hasAnyError) {
    return <Trans>Failed retrieving available promo codes</Trans>
  }

  return useObserver(() => (
    <SelectControl<TCode>
      options={result.data ? result.data.promoCodes : []}
      isLoading={result.loading}
      isMulti={true}
      isClearable={true}
      getOptionValue={RM.prop('id')}
      getOptionLabel={RM.prop('code')}
      placeholder={i18n.t`Promo codes`.toLocaleLowerCase()}
      noOptionsMessage={() => i18n.t`No promo codes available`}
      value={Array.from(selectedCodes.values())}
      onChange={value => {
        if (Array.isArray(value)) {
          selectedCodes.replace(value.map(code => [code.id, code]))
        } else {
          selectedCodes.clear()
        }
        // const codes = value as ReadonlyArray<TCode>
        // if (action === 'select-option') {
        //   const code = _.last(codes)!
        //   selectedCodes.push(code)
        //   onCodeAdd(code)
        // }
        // if (action === 'remove-value') {
        //   RM.differenceWith(
        //     (lCode, rCode) => lCode.id === rCode.id,
        //     selectedCodes,
        //     codes,
        //   ).forEach(onCodeRemove)
        //   selectedCodes.replace([...codes])
        // }
        // if (action === 'clear') {
        //   codes.forEach(onCodeRemove)
        //   selectedCodes.clear()
        // }
      }}
    />
  ))
}
